body
  transition: opacity 0.5s ease

body.changing-theme
  pointer-events: none
  opacity: 0

#theme-dropdown .change-theme-trigger
  font-size: 1.4em

#theme-pannel
  padding-left: baseline(20)

#theme-pannel .theme-pannel-trigger,
#theme-pannel .theme-pannel-option label
  padding: 0 baseline(1.5)